




























import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins} from "vue-property-decorator";

@Component
export default class ImportSequenceModal extends Mixins(AsModal) {
  @Emit("refresh") refresh(): void {}

  formData = {json: "", sequence_name: "", content: "", note: ""};
  fileList = [];
  add(): void {
    this.refresh();
    this.clean();
  }

  clean(): void {
    this.formData = {json: "", sequence_name: "", content: "", note: ""};
    this.dismiss();
  }

  importJSON(): void {
    navigator.clipboard.readText().then((text) => (this.formData.content = text));
  }

  upload(options: {file: File}): void {
    const fileReader = new FileReader();
    fileReader.readAsText(options.file);
    fileReader.onload = () => {
      console.log(fileReader.result);
      this.formData.content = fileReader.result as string;
    };
  }
}
